header{
    height:150vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height:100%;
    position:relative;


}
/* Syle CTA */
.cta{
    margin-top: 2.5rem;
    display:flex;
    gap:1.2rem;
    justify-content: center;
}
/* Header Socials */
.header_socials{
    display: flex;
    flex-direction:column;
    gap: 0.8rem;
    position: absolute;
    left:0;
    bottom:0.5rem;
    
}
.header_socials::after{
    content:"";
    width:1px;
    height:2rem;
    background:var(--color-primary);

}

/* Image */
.me{
    background: linear-gradient(var(--color-primary),transparent);
    width:30rem;
    height:100%;
    /* width:100%;
    aspect-ratio: 1/1; */
   
    position:absolute;
    left:calc(20%);
    margin-top: 10rem;
    border-radius: 4rem 4rem 0 0;
    overflow: hidden;
    padding: 1rem 1.5rem 1.5rem 1.5rem;
}
.scroll_down{
    position: absolute;
    right: -2.3em;
    bottom:2.5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;

}
/* Medium Devices */
@media screen and ( max-width:1024px) {
    header{
        height:100vh;


    }
    .me{
        background: linear-gradient(var(--color-primary),transparent);
        width:20rem;
        height:30rem;
        /* width:100%;
        aspect-ratio: 1/1; */
       
        position:absolute;
        left:calc(30%);
        margin-top: 1rem;
        border-radius: 4rem 4rem 0 0;
        overflow: hidden;
        padding: 1rem 1.5rem 1.5rem 1.5rem;
    }

}

/* Medium Devices */
@media screen and ( max-width:600px) {
    header{
        height:100vh;
        
    }
    .header_socials,.scroll_down{
        display: none;
    }
    .me{
        background: linear-gradient(var(--color-primary),transparent);
        width:20rem;
        height:30rem;
        /* width:100%;
        aspect-ratio: 1/1; */
       
        position:absolute;
        left:calc(20%);
        margin-top: 1rem;
        border-radius: 4rem 4rem 0 0;
        overflow: hidden;
        padding: 1rem 1.5rem 1.5rem 1.5rem;
    }
}